'use strict';

//import angular from 'angular';
const angular = require('angular');
const ngRoute = require('angular-route');
import routes from './league.routes';

export class LeagueComponent {
  $http;
  //leaguePlayers = [];
  sortType;
  sortReverse;
  leagueName;

  /*ngInject*/
  constructor($http, $scope) {
    'ngInject';

    this.$http = $http;


    //$scope.message = '111';
    console.log('111111');


  }

  $onInit() {
    this.message = 'Hello';
    this.sortType = 'live_data.live_total';
    this.sortReverse = true;
    console.log('2222');
    //var getLeagueData = function () {
      this.$http.get('/api/leagues').then(resp => {
        console.log('/api/leagues', resp.data.league.name, resp.data.standings.results);
        this.leaguePlayers = resp.data.standings.results;
        this.leagueName = resp.data.league.name;
      });
    // };
    // getLeagueData();
  }
}

export default angular.module('fan4lifeApp.league', [ngRoute])
  .config(routes)
  .component('league', {
    template: require('./league.html'),
    controller: LeagueComponent,
    controllerAs: 'leagueCtrl'
  })
  .name;



